#navigation {
  width: 100%;
  font-weight: 300;
}
#navigation ul {
  list-style: none;
  margin: 0;
  display: flex;
  justify-content: center;
  padding: 0;
}
#navigation ul li {
  padding: 13px 20px 15px 20px;
  cursor: pointer;
  font-size: 20px;
  -webkit-transition: color .25s;
  transition: color .25s;
}
#navigation ul li a{
  text-decoration: none;
  color: #f4f4f4;
}
#navigation ul li a:hover, #navigation ul li a.active-nav-item {
  color: #96ccff;
}

@media all and (max-width: 640px) {
  #navigation ul li {
    padding: 7px 10px 9px 10px;
    font-size: 15px;
  }
}