header{
  background-color: #282c34;
  display: block;
  color: white;
  overflow: hidden;
  position: fixed;
  width: 100%;
  min-width: 320px;
  z-index: 9999;
}

#app-header {
  max-width: 980px;
  margin: 0 auto;
}

#app-logo {
  width: 30%;
  float: left;
}

#logo {
  animation: app-logo-spin infinite 8s linear;
  width: 65px;
  padding: 3px 0 0 0;
  cursor: pointer;
}

@keyframes app-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

