html {
	scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

a {
  color: #61dafb;
}
#navigation {
  width: 100%;
  font-weight: 300;
}
#navigation ul {
  list-style: none;
  margin: 0;
  display: flex;
  justify-content: center;
  padding: 0;
}
#navigation ul li {
  padding: 13px 20px 15px 20px;
  cursor: pointer;
  font-size: 20px;
  -webkit-transition: color .25s;
  transition: color .25s;
}
#navigation ul li a{
  text-decoration: none;
  color: #f4f4f4;
}
#navigation ul li a:hover, #navigation ul li a.active-nav-item {
  color: #96ccff;
}

@media all and (max-width: 640px) {
  #navigation ul li {
    padding: 7px 10px 9px 10px;
    font-size: 15px;
  }
}
header{
  background-color: #282c34;
  display: block;
  color: white;
  overflow: hidden;
  position: fixed;
  width: 100%;
  min-width: 320px;
  z-index: 9999;
}

#app-header {
  max-width: 980px;
  margin: 0 auto;
}

#app-logo {
  width: 30%;
  float: left;
}

#logo {
  -webkit-animation: app-logo-spin infinite 8s linear;
          animation: app-logo-spin infinite 8s linear;
  width: 65px;
  padding: 3px 0 0 0;
  cursor: pointer;
}

@-webkit-keyframes app-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes app-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


#portfolios {
	width: 100%;
	margin: 0;
	padding: 3vh 10px 10vh 10px;
	min-height:100vh;
	display: flex;
	background-image: url(/static/media/bg-portfolios.da2ba89f.jpg); 
	color: white;
}
#portfolios .projects {
	max-width: 980px;
	margin: 0 auto;
}

#portfolios .project {
	background-color: rgba(40, 44, 52, 0.95);
}

#portfolios .project:hover {
	
}
#introduction {
	display: block;
	margin: 0;
	background-image: url(/static/media/bg-introduction.b68a9225.jpg); 
	color: white;
	min-height:100vh;
	min-width: 320px;
	width: 100%;
	text-align: center;
}
.introduction {
	background: rgba(0,38,160,.2);
  background: -webkit-linear-gradient(bottom,rgba(0,8,0,.7),rgba(10,0,0,.3));
  background: linear-gradient(0deg,rgba(0,8,0,.7),rgba(10,0,0,.3));
  min-height:100vh;
  width: 100%;
  display: flex;
	justify-content: center;
	align-items: center;
	padding: 50px 10px 0 10px;
}

#introduction h1 {
	font-weight: 400;
	padding: 0px 10px 40px 10px;
	margin: 0;
	font-size: 50px!important;
}

#introduction h3 {
	font-weight: 400;
	padding: 0px 10px 10px 10px;
	margin: 0;
	font-size: 28px!important;
}

#introduction h4 {
	font-weight: 400;
	margin: 0;
	padding: 0px 10px 15px 10px;
	font-size: 20px!important;
}

#introduction .tilt {
	background: -webkit-linear-gradient(bottom, #0ba3a5 0%, #04C8DE 100%);
	background: linear-gradient(0deg, #0ba3a5 0%, #04C8DE 100%);
}

#introduction .hover-glow:hover {
	text-shadow: 0 0 5px silver;
	-webkit-transition: opacity 0.25s linear, text-shadow 0.25s linear;
	transition: opacity 0.25s linear, text-shadow 0.25s linear;
}

#introduction article {

}

@media all and (max-width: 640px) {
	#introduction {
		padding: 35px 0 0 0;
	}
	#introduction h1 {
		font-size: 26px!important;
		padding: 0 0 30px 0;
	}

	#introduction h3 {
		font-size: 18px!important;
		padding: 0;
	}

	#introduction h4 {
		padding: 0 10px 10px 10px;
		font-size: 16px!important;
	}


}
